@import '../App.scss';


.section-right {
    width: 65%;
    background-color: white;
    .title {
      margin-top: $base*60;
      height: $base*100;
      background-color: $second-color;
      .name {
         padding: $base*34 0 0 $base*31;
      }
      .professional-title {
          padding: $base*11 0 0 $base*31;
       }
  }
  .section-abou-me {
      padding-left: $base*31;
      padding-bottom: $base*26;
      padding-right: $base*40;
      
      &-header {
          padding-bottom: $base*11;
          padding-top : $base*28;
          border-bottom: $base solid $firtst-color;
      }
      &-text {
          padding-top: $base*8;
          a {
            text-decoration: none;
            text-transform: uppercase;
            display: flex;
            justify-content:flex-end;
            h4:hover {
                text-shadow: 1px 1px 5px  $firtst-color;
              }
          }
          
      }
      
  }
  .section-work {
      padding-left: $base*31;
      padding-bottom: $base*26;
      padding-right: $base*40;
  
      &-header {
          padding-bottom: $base*11;
          border-bottom: $base solid $firtst-color;
      }
      &-context {
          padding-top: $base*20;
          display: flex;
          flex-wrap: wrap;
          h4 {
              font-weight: 700;
          }
          &-years {
              width: 30%;
          }
          &-main {
              width: 60%;
              &-company {
                  padding-top: $base*7;
              }
              &-text {
                  padding-top: $base*10;
                  h4 {
                      font-weight: 600;
                  }
              }
          }
      }
      &-skill {
          padding-left: $base*92;
          padding-right: $base*7;
          padding-top: $base*20;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          &-item {
              width:45%;
              padding-bottom: $base*12;
              &-rage {
                  margin-top: $base*9;
                  width: 100%;
                  height: $base*1.3;
                  background-color: #cccccc;
                  &-width {
                      display: flex;
                      content: '';
                      width: 50%;
                      height: $base*1.8;
                      background-color: #2d2d2d;
                  } 
              }
          }
      }
  }
  }